.App {
  margin: auto;
  text-align: center;
  width: 430px;
  border: solid #282c34;
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 50px;
}

.App-logo {
  height: 60px;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 25px;
  color: white;
}

.yearOverridden {
  background-color: forestgreen;
  min-height: 275px;
}

.YearSelectorDropdown {
  font-size:20px;
}

.YearSelectorButton {
  font-size: 20px;
  border: none;
  background-color: forestgreen;
  color: white;
  border-radius: 4px;
  cursor: pointer;
}

.YearSelectorButton:hover {
  background-color: rgb(42, 179, 42);
}

.PairsTable {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-collapse: collapse;
}

.PairsTable td,
.PairsTable th {
  border: 1px solid rgb(202, 199, 199);
  padding: 8px;
  width: 100px;
}

.PairsTable tr:nth-child(even) {
  background-color: rgb(202, 199, 199);
}

.PairsTable tr:nth-child(odd) {
  background-color: white;
}

.PairsTable th {
  text-align: center;
  background-color: forestgreen;
  color: white;
}

.yearOverriddenSide {
  position: absolute;
}

.yearOverriddenSideHidden {
  visibility: hidden;
  position: absolute;
}

.yearOverriddenSideSVG{
  width: 150px;
  height: 30px;
  transform: translate(-40px, 150px) rotate(-90deg);
}

.yearOverriddenSideSVGText{
  fill: white;
  font-size: 28px;
}

.standardButton {
  font-size: 12px;
  cursor: pointer;
  background-color: forestgreen;
  color: white;
  border: solid white;
  border-width: 1px;
  border-radius: 4px;
}

.standardButton:hover {
  background-color: rgb(42, 179, 42);
}

.RandomizedTable {
  background-color: #282c34;
  border-bottom-left-radius: 45px;
  border-bottom-right-radius: 45px;
}

.RandomizerH3{
  color: white;
}
